    function redirect(page) {
        var redirect = "";
        if(typeof page == "undefined" || page == null) {
            redirect = '/sok/reg/kompletta-hjul/falgar';
        } else {
            redirect = '/sok/reg/kompletta-hjul/falgar?page=' + page;
        }

        if (history.pushState) {
          window.history.pushState("", "", redirect);
        } else {
          document.location.href = redirect;
        }
        return;
    }


    $(document).on('click', '.pagination a', function(e) {
        e.preventDefault();
        var url = $(this).attr('href');
        var page = $(this).text();
        var textSearch = $('#textSearch').val();
        var brand = $(".DDBrands").val();
        var directNav = $('.check-btn [name="directNav"]').is(':checked') ? 1:0;
        redirect(page);

        $.ajax({
            type: 'GET',
            url: url, 
            data: {
                textSearch: textSearch,
                brand: brand,
                directNav: directNav,
            },
            dataType: 'json',
            xhrFields: {
                withCredentials: true
            },
            success: function(data) {
                $('#searchResult').remove();
                $('.categoryFooter').remove();
                $('#searchContainer div.col-sm-12').append(data.searchResult);

                $('html, body').animate({scrollTop : 0},1000);
            }
        });
    });

    $(document).on('click', '.selectSizes', function(e) {
        e.preventDefault();
        $(this).addClass('active').siblings().removeClass('active');

        var textSearch = $('#textSearch').val();
        var size = $(this).data('size');
        var brand = $(".DDBrands").val();
        var directNav = $('.check-btn [name="directNav"]').is(':checked') ? 1:0;
        redirect();

        $.ajax({
            type: 'get',
            url: 'falgar',
            data: {
                textSearch: textSearch,
                size: size,
                brand: brand,
                directNav: directNav,
                page: 1,
            },
            dataType: 'json',
            xhrFields: {
                withCredentials: true
            },
            success: function(data) {
                $('#searchResult').remove();
                $('.selectDimensions').remove();
                $('.categoryFooter').remove(); 
                $('#productCount').empty();
                $('#productCount').html(data.productCount);
                $('#searchContainer div.col-sm-12').append(data.searchResult);
                $('.dimensions').append(data.dimensions);
                
            }
        });
    });

